import React from 'react'
import Link from 'gatsby-link'

const AllTags = ({ pathContext }) => {
  const { tags } = pathContext
  
  return tags && (
    <div>
      <ul>
        {tags.map(tag => {
          return (
            <li>
              <Link to={`/tags/${tag}`}>
                {tag}
              </Link>
            </li>
          )
        })}
      </ul>
    </div>
  ) || <span />
}

export default AllTags



